<template>
  <div class="button">
    <slot></slot>
    <span>{{buttonName}}</span>
  </div>
</template>
<script>
  export default {
    name: 'Button',
    props: {
      buttonName: String
    }
  }
</script>
<style lang="less">
  @import "./button";
</style>



