<template>
  <div class="dev-switch" :class="swtichState==true?'on':'off'">
    <!--设备信息-->
    <div class="switch-information">
      <div class="information-content">通道号:01</div>
      <div class="information-content">型号:XJY-ZN-03-3P</div>
    </div>
    <!--开关按钮-->
    <div class="group-switch">
      <div class="group-item">
        <div class="switch-icon" @click="switchControl">
          <i class="iconfont icon-kaiguan"></i>
        </div>
        <div class="content">{{swtichState == true ? '开关电源已开启' : '开关电源已关闭'}}</div>
      </div>
    </div>
    <!--功能按钮-->
    <fun-button>
      <fun-item
        icon="icon-kaiguan"
        content="开关"
        @click.native="switchControl">
      </fun-item>
      <fun-item
        icon="icon-shijian"
        content="点动"
        @click.native="pointMove">
      </fun-item>
      <fun-item
        icon="icon-rename"
        content="重命名"
        @click.native="rename">
      </fun-item>
    </fun-button>
  </div>
</template>
<script>
  import {FunButton, FunItem} from '../components/button'
  import {MessageBox} from 'mint-ui'

  export default {
    name: 'DevSwitch',
    data () {
      return {
        swtichState: true
      }
    },
    components: {
      'fun-button': FunButton,
      'fun-item': FunItem
    },
    methods: {
      //开关控制
      switchControl: function () {
        this.swtichState = !this.swtichState
      },
      //点动
      pointMove: function () {
        if (this.swtichState) {
          MessageBox('提示', '开关开启中,无法进行点动设置')
        } else {
          this.$router.push('/dd-setting')
        }
      },
      //重命名
      rename: function () {
        MessageBox.prompt('开关名称一经修改立即生效', '重命名', {inputPlaceholder: '通道01'})
          .then(({value}) => {
            //确认操作
            console.log(value)
          }).catch(() => {
          //取消操作

        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .dev-switch {
    &.on {
      background: radial-gradient(#38cfff 0%, #2f88ff 100%);
    }
    &.off {
      background: radial-gradient(#c7c6cb 0%, #908f94 100%);
    }
    .switch-information {
      padding: 10/37.5rem;
      font-size: 15/37.5rem;
      text-align: right;
      color: #fff;
      .information-content {
        margin-bottom: 6/37.5rem;
      }
    }
    .group-switch {
      width: 100%;
      padding: 10/37.5rem;
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      -webkit-transform: translateY(-70%);
      transform: translateY(-70%);
      .group-item {
        width: 100%;
        height: 100%;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-align-items: center;
        align-items: center;
        background-color: transparent;
        color: #fff;
        -webkit-flex-direction: column;
        flex-direction: column;
        .switch-icon {
          img {
            width: 200px;
            height: 200px;
          }
          .iconfont {
            font-size: 180/37.5rem;
            display: block;
          }
        }
        .content {
          font-size: 18/37.5rem;
          margin: 10/37.5rem auto;
        }
      }
    }
  }
</style>



