<script>
  export default {
    name: 'Share'
  }
</script>
<style lang="less" scoped>
  .share {
    background: radial-gradient(#38cfff 0%, #2f88ff 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    .qr-code{
      text-align: center;
      .code-img{
        width: 150/37.5rem;
        height: 150/37.5rem;
        padding: 10/37.5rem;
        background-color: #fff;
        border-radius: 6/37.5rem;
