<template>
  <div class="list-item">
    <div class="item-title">
      <router-link :to="settingPath" class="title-link">
        <div class="item-l">
          <div class="signal" :class="{'no-signal':signalActive}">
            <div class="signal-one"></div>
          </div>
          <div class="item-l_content">
            <div class="content-title">{{title}}</div>
          </div>
        </div>
        <div class="item-r">
          <i class="iconfont icon-shezhi"></i>
        </div>
      </router-link>
    </div>
    <div class="item-info">
      <div class="clearfix" :class="[flexOne==true?'info-flex_one':'info-flex']">
        <router-link v-for="(item,index) in infoItem" :to="portPath" class="flex-item" :key="index">
          <div class="item-in">
            <div class="in-l">
              <div class="title">{{item.port_name}}</div>
              <div class="content">状态:{{item.port_status==1?'开':'关'}}</div>
            </div>
            <div class="in-r">
              <img src="../../assets/img/switch-icon.png" alt="" class="switch-img">
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'DevItem',
    props: {
      signalActive: {
        type: Boolean,
        default: false
      },
      title: String,
      settingPath: Object,
      portPath: Object,
      infoItem: Array,
      flexOne: {
        type: Boolean,
        default: false
      }
    }
  }
</script>



