<template>
  <div class="fun-item">
    <div class="item-icon">
      <i class="iconfont" :class="icon"></i>
    </div>
    <div class="content">{{content}}</div>
  </div>
</template>
<script>
  export default {
    name: 'FunItem',
    props: {
      icon: String,
      content: String
    }
  }
</script>



