import Vue from 'vue'
import Router from 'vue-router'
import Home from '../view/Home.vue'
import EnterPassword from '../view/EnterPassword.vue'
import Setting from '../view/Setting.vue'
import Share from '../view/Share.vue'
import DevSwitch from '../view/DevSwitch.vue'
import DdSetting from '../view/DdSetting.vue'

Vue.use(Router)

Router.prototype.goBack = function () {
  this.isBack = true
  window.history.go(-1)
}
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: '开关列表',
      key: 1
    }
  },
  {
    path: '/dev-switch',
    name: 'DevSwitch',
    component: DevSwitch,
    meta: {
      title: '开关',
      key: 2
    }
  },
  {
    path: '/setting',
    name: 'Setting',
    component: Setting,
    meta: {
      title: '设置',
      key: 2
    }
  },
  {
    path: '/share',
    name: 'Share',
    component: Share,
    meta: {
      title: '分享',
      key: 3
    }
  },
  {
    path: '/enter-password',
    name: 'EnterPassword',
    component: EnterPassword,
    meta: {
      title: '输入密码',
      key: 2
    }
  },
  {
    path: '/dd-setting',
    name: 'DdSetting',
    component: DdSetting,
    meta: {
      title: '点动设置',
      key: 3
    }
  }
]

const router = new Router({
  linkActiveClass: 'active',
  scrollBehavior: function (to, from, savedPosition) {
    return savedPosition || {x: 0, y: 0}
  },
  routes: routes
})

router.beforeEach((toRoute, fromRoute, next) => {
  if (toRoute.meta.title) {
    document.title = toRoute.meta.title
  }
  next()
})

export default router



