<template>
  <a href="javascript:;" class="footer-item add">
    <i class="iconfont" :class="icon"></i>
    <p>{{content}}</p>
  </a>
</template>
<script>
  export default {
    name: 'Footer',
    props: {
      icon: {
        type: String
      },
      content: {
        type: String
      }
    }
  }
</script>




