<template>
  <div class="home contain-footer body-c">
    <!--设备列表-->
    <dev-list>
      <dev-item
        v-for="(item,index) in getData"
        :key="index"
        :signalActive="item.online=='0'?true:false"
        :title="item.name"
        :settingPath="{path:'/setting',query:{name:item.name,uid:uid,devId:item.devid}}"
        :infoItem="item.port_info"
        :portPath="{path:'/dev-switch'}"
        :flexOne="item.total_port==1?true:false">
      </dev-item>
    </dev-list>
    <!--footer-->
    <footer-page>
      <footer-item
        @click.native="addDevice"
        icon="icon-jia"
        content="添加设备">
      </footer-item>
    </footer-page>
  </div>
</template>
<script>
  import {DevList, DevItem} from '../components/dev-list'
  import {Footer, FooterItem} from '../components/footer'
  import {MessageBox} from 'mint-ui'

  export default {
    name: 'Home',
    data () {
      return {
        uid: 1,
        getData: null
      }
    },
    components: {
      'dev-list': DevList,
      'dev-item': DevItem,
      'footer-page': Footer,
      'footer-item': FooterItem
    },
    created () {
      const self = this
      self.$http.get('/getSwitchList', {
        params: {
          uid: self.uid
        }
      }).then(function (res) {
        if (res.status == '200') {
          console.log(res.data);
          self.getData = res.data.devInfo
        }
      }).catch(function (res) {
        console.log(res);
      })
    },
    methods: {
      //添加设备
      addDevice: function () {
        this.$router.push('/enter-password')
      }
    }
  }
</script>



