<template>
  <div class="enter-password">
    <div class="pas-info hidden">
      <div class="password">
        <i class="iconfont icon-mima1 icon"></i>
        <input type="text" placeholder="请输入密码" v-model.trim="password">
      </div>
    </div>
    <button-view buttonName="确认" @click.native="confirm"></button-view>
  </div>
</template>
<script>
  import {Button} from '../components/button'

  export default {
    name: 'EnterPassword',
    components: {
      'button-view': Button
    },
    data () {
      return {
        password: ''
      }
    },
    methods: {
      //确认按钮
      confirm: function () {
        console.log(this.password);
      }
    }
  }
</script>
<style lang="less" scoped>
  .enter-password {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .pas-info {
      width: 100%;
      margin: 15/37.5rem auto;
      .password {
        width: 85%;
        height: 35/27.5rem;
        border: 1px solid #2f88ff;
        margin: 0 auto;
        border-radius: 50/37.5rem;
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
        align-items: center;
        -webkit-justify-content: center;
        justify-content: center;
        padding: 0 15/37.5rem;
        .icon {
          margin-right: 10/37.5rem;
          color: #2f88ff;
          font-size: 20/37.5rem;
        }
        input {
          width: 100%;
          height: 100%;
          font-size: 15/37.5rem;
          background-color: transparent;
          color: #2f88ff;
        }
        input::-webkit-input-placeholder {
          color: #888;
        }
        input::-moz-placeholder {
          color: #888;
        }
        input:-moz-placeholder {
          color: #888;
        }
        input:-ms-input-placeholder {
          color: #888;
        }
      }
    }
  }
</style>



