<template>
  <div class="dd-setting body-c">
    <!--提示-->
    <div class="setting-title">在此时间之后执行动作</div>
    <!--时间统计-->
    <div class="time-val">
      <input type="text" id="dayInput" value="0" v-model="day" readonly="">天
      <input type="text" id="hoursInput" value="0" v-model="hours" readonly="">小时
      <input type="text" id="minutesInput" value="0" v-model="minutes" readonly="">分钟
    </div>
    <!--时间选择-->
    <picker-view
      class="body-f"
      :slots="slots"
      @change="onValuesChange">
    </picker-view>
    <!--启动按钮-->
    <button-view buttonName="启动" @click.native="start"></button-view>
  </div>
</template>
<script>
  import {Button} from '../components/button'
  import {Picker} from 'mint-ui'

  export default {
    name: 'DdSetting',
    components: {
      'button-view': Button,
      'picker-view': Picker
    },
    data () {
      return {
        day: '',
        hours: '',
        minutes: '',
        slots: [
          {
            flex: 1,
            values: this.daySum(),
            className: 'slot1',
            textAlign: 'center'
          },
          {
            flex: 1,
            values: this.hoursSum(),
            className: 'slot2',
            textAlign: 'center'
          },
          {
            flex: 1,
            values: this.minutesSum(),
            className: 'slot3',
            textAlign: 'center'
          }
        ]
      }
    },
    methods: {
      //天数
      daySum:function () {
        let sum = [];
        for (var i = 0; i < 100; i++) {
          sum.push(i)
        }
        return sum
      },
      //小时
      hoursSum: function () {
        let sum = [];
        for (var i = 0; i < 12; i++) {
          sum.push(i)
        }
        return sum
      },
      //分钟
      minutesSum: function () {
        let sum = [];
        for (var i = 0; i < 60; i++) {
          sum.push(i)
        }
        return sum
      },
      //时间选择
      onValuesChange (picker, values) {
        this.day = values[0]
        this.hours = values[1]
        this.minutes = values[2]
      },
      //启动
      start: function () {
        const data = {
          day: this.day,
          hours: this.hours,
          minutes: this.minutes
        }
        this.$router.goBack()
      }
    }
  }
</script>
<style lang="less" scoped>
  .setting-title {
    padding: 10/37.5rem;
    color: #999;
    font-size: 15/37.5rem;
  }

  .time-val {
    padding: 10/37.5rem;
    font-size: 14/37.5rem;
    input {
      width: 20/37.5rem;
      background-color: transparent;
      font-size: 18/37.5rem;
      text-align: center;
      margin-top: -3/37.5rem;
    }
  }
</style>



